body{
  background-color: whitesmoke;
}

#mainContainer{
  width: 90%;
  background-color: rgb(244,247,250);
  margin: 0 auto;

}

header{
  font-size: 2.2rem;
  font-weight: 600;
  background-color: rgb(7,24,75);
  color: whitesmoke;
  padding: 2%;

}

header img{
  height: 70px;
  margin-right: 2%;
  float: left;
  margin-bottom: 2%;
  display: flex;
  align-items: center;
}

.med{
  height: 160px;
  width: 160px;
}

#mainHeadingText{
  display: flex;
  align-items: center;
}

.small{
  float: left;
  height: 36px;
  padding-right: 4%;
}

.scrollStyle, .scrollStyleLarge
{
    overflow-y: scroll;
    border: 1px solid darkgray;
    box-shadow: 10px 10px 10px lightgray;
}
.scrollStyle{
  max-height: 500px;
}


.scrollStyleLarge{
  max-height: 1100px;
}


.margin-left-1{
  margin-left: 1%;
  margin-right: 1%;
}